import React from 'react';
import { Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle} from 'reactstrap';
import { Loading } from './LoadingComponent';
import { FadeTransform } from 'react-animation-components';
function RenderCard({item, isLoading, errMess}) {
    if (isLoading) {
        return(
                <Loading />
        );
    }
    else if (errMess) {
        return(
                <h4>{errMess}</h4>
        );
    }
    else 
        return(
            <FadeTransform
            in
            transformProps={{
                exitTransform: 'scale(0.5) translateY(-50%)'
            }}>
            <Card>
                <CardImg src={item.image} alt={item.name} />
                <CardBody>
                <CardTitle>{item.name}</CardTitle>
                {item.designation ? <CardSubtitle>{item.designation}</CardSubtitle> : null }
                <CardText>{item.description}</CardText>
                </CardBody>
            </Card>
        </FadeTransform>
        );
}

function Home(props) {
    
    return(
        <div className="container">
            <div className="row align-items-start py-4">
                <div className="col-12 col-md m-1">
                    <h5>Dish</h5>
                    <RenderCard item={props.dish} isLoading={props.dishesLoading} errMess={props.dishErrMess} />
                </div>
                <div className="col-12 col-md m-1">
                <h5>Promotion</h5>
                    <RenderCard item={props.promotion} isLoading={props.promoLoading} errMess={props.promoErrMess} />
                </div>
                <div className="col-12 col-md m-1">
                <h5>Leader</h5>
                    <RenderCard item={props.leader} isLoading={props.leadersLoading} errMess={props.leaderErrMess} />
                </div>
            </div>
        </div>
    );
}

export default Home;